.container {
  max-width: 1400px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
}

.search-form {
  margin-bottom: 20px;
}

.search-form input {
  width: 70%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.photo-card {
  text-align: center;
  max-width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  display: none;
}

